import * as React from "react"
import SEO from "../../components/seo";
import { cursor, cursorTypesCodeString } from "../../data/cursor";
import { CodeSample, SectionWrapper } from "../../components/ExampleComponents";

const Cursor = () => {
    return (
        <>
            <SEO title={ "Cursor" }/>
            <h1>Cursor</h1>
            <p>The class constructor looks like this:</p>
            <CodeSample label={ "For Cursor" } codeString={ `cursor--{type}` }/>
            <SectionWrapper id={'cursor-types'} codeString={ cursorTypesCodeString.join( '' ) }>
                { cursor.map( cursor => (<div key={ cursor } className={ `p--md mb--sm bg--bg text--text cursor--${ cursor }` }>.cursor--{ cursor }</div>) ) }
            </SectionWrapper>
        </>
    )
}

export default Cursor
